"use client";

import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import roadmapData from "./index"; // âœ… Import both frontend and backend roadmaps

export default function Page() {
  // Selected category: Frontend or Backend
  const [selectedCategory, setSelectedCategory] = useState("Frontend");
  // Selected topic within category
  const [selectedTopic, setSelectedTopic] = useState("");

  // Get all categories (Frontend, Backend, etc.)
  const categories = Object.keys(roadmapData);

  // Get topics for the selected category
  const topics =
    typeof roadmapData[selectedCategory] === "object"
      ? Object.keys(roadmapData[selectedCategory])
      : [];

  // Update selectedTopic whenever the category changes
  useEffect(() => {
    setSelectedTopic(topics[0] || ""); // default to first topic
  }, [selectedCategory]);

  // Get the selected topic content
  const selectedContent =
    selectedTopic && roadmapData[selectedCategory]
      ? roadmapData[selectedCategory][selectedTopic]
      : null;

  return (
    <div className="flex w-full h-screen bg-gradient-to-br from-gray-950 via-gray-900 to-black text-white overflow-hidden">
      {/* Sidebar */}
      <div className="w-64 bg-gradient-to-b from-gray-900 to-black border-r border-gray-800 p-6 flex flex-col">
        <h1 className="text-3xl font-extrabold mb-10 text-indigo-400 tracking-wide">
          Skill<span className="text-white">Forge</span>
        </h1>

        {/* Category Buttons */}
        {categories.map((category) => (
          <button
            key={category}
            className={`mb-3 p-3 text-left rounded-xl font-medium transition-all duration-200 ${
              selectedCategory === category
                ? "bg-indigo-600 text-white shadow-lg shadow-indigo-600/30"
                : "bg-gray-900 hover:bg-gray-800"
            }`}
            onClick={() => setSelectedCategory(category)}
          >
            {category}
          </button>
        ))}
      </div>

      {/* Main Content */}
      <div className="flex-1 px-10 py-8 overflow-y-auto">
        <h2 className="text-4xl font-bold mb-6 text-indigo-400">
          {selectedCategory} Roadmap
        </h2>

        {/* Topic Buttons */}
        {topics.length > 1 && (
          <div className="flex gap-3 mb-6 flex-wrap">
            {topics.map((topic) => (
              <button
                key={topic}
                className={`px-4 py-2 rounded-lg text-sm font-semibold transition-all ${
                  selectedTopic === topic
                    ? "bg-indigo-600 text-white"
                    : "bg-gray-800 hover:bg-gray-700"
                }`}
                onClick={() => setSelectedTopic(topic)}
              >
                {topic}
              </button>
            ))}
          </div>
        )}

        {/* Content */}
        <motion.div
          className="space-y-6"
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
        >
          {selectedContent && selectedContent.sections ? (
            <div className="bg-gray-900/60 p-6 rounded-xl border border-gray-800 backdrop-blur-md">
              {/* Topic Title */}
              <h3 className="text-2xl font-bold text-indigo-400 mb-2">
                {selectedContent.title}
              </h3>
              {/* Topic Objective */}
              <p className="text-gray-300 mb-6">{selectedContent.objective}</p>

              {/* Sections */}
              {selectedContent.sections.map((section, index) => (
                <div key={index} className="mb-6">
                  <h4 className="text-xl font-semibold text-indigo-300 mb-2">
                    {section.title}
                  </h4>
                  <ul className="list-disc list-inside text-gray-300 space-y-1">
                    {section.points.map((point, i) => (
                      <li key={i}>{point}</li>
                    ))}
                  </ul>
                </div>
              ))}
            </div>
          ) : (
            <p className="text-gray-400">No roadmap content available.</p>
          )}
        </motion.div>
      </div>
    </div>
  );
}
